import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'
import {start, Start, rainbow, StyledText, Content, ContentGroup, Parent, Sub, Body, StyledLink, Glow, BodyImage} from '../components/styles.js'

class HeliaMono extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false,
      hover3: false,
      hover4: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <p>
                <Glow>
                Helia Mono
                </Glow>

                <Sub>
                Nov 2020
                </Sub>

                <Body>
                Helia Mono is a monospace font I designed as my 2D Design final project.
                Originally intended to be an upbeat and slightly off-kilter coding font, it ended up being more suited towards large stylized all-caps headlines, working especially well on posters.
                I also created some collateral mockups as potential ideas of where the font might be best used.
                </Body>

                <BodyImage>
                <img src={require('../assets/images/helia1.jpg')} />
                <img src={require('../assets/images/helia2.jpg')} />
                <img src={require('../assets/images/helia3.jpg')} />
                <img src={require('../assets/images/helia4.jpg')} />
                <img src={require('../assets/images/helia5.jpg')} />
                <img src={require('../assets/images/helia6.jpg')} />
                </BodyImage>
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default HeliaMono



